import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css']
})
export class OrderComponent implements OnInit {

  title = 'Order Booking';
  countItems = 0;

  ItemList = {};
  ItemListDetail = [];

  ItemName = '';
  Qty = '';
  Rate = '';
  Amount = '';

  constructor() { }

  ngOnInit() {
  }


  addTask()  {
    this.ItemList = {
      itmName: this.ItemName,
      itmQty: this.Qty,
      itmRate: this.Rate,
      itmAmount: this.Amount,
    };

    this.ItemListDetail.push(this.ItemList);
    this.ItemName = '';
    this.Qty = '';
    this.Rate = '';
    this.Amount = '';
    this.countItems = this.ItemListDetail.length;

  }
  removeTask(index) {
    this.ItemListDetail.splice(index, 1);
    this.countItems = this.ItemListDetail.length;
  }
}
